import { useEffect } from "react";

import { getActiveValue } from "../state/util";
import { IState } from "../types";

export const useScrollToBlock = (state: IState, scrollRef: React.RefObject<HTMLElement>) =>
  useEffect(() => {
    if (!scrollRef.current) {
      return;
    }
    const activeValue = getActiveValue(state);
    if (activeValue === null || activeValue.idRef === null) {
      return;
    }
    const block: HTMLElement | null = document.querySelector(`#support-${activeValue.idRef}`);
    if (block) {
      scrollRef.current.style.scrollBehavior = "smooth";
      let offsetTop = block.offsetTop;
      let parent: HTMLElement = block.offsetParent as HTMLElement;
      while (!!parent && parent !== scrollRef.current) {
        offsetTop += parent.offsetTop;
        parent = parent.offsetParent as HTMLElement;
      }
      const { clientHeight } = block;
      // if the element is larger than the view port, we scroll to the top of the element
      if (clientHeight > scrollRef.current.clientHeight) {
        scrollRef.current.scrollTop = offsetTop;
        return;
      }
      // center the element in the scroll view
      const offsetY = Math.floor(offsetTop - (scrollRef.current.clientHeight - clientHeight) / 2);
      scrollRef.current.scrollTop = Math.max(0, Math.min(scrollRef.current.scrollHeight, offsetY));
    }
  }, [
    state.activeDataPoint.groupIndex,
    state.activeDataPoint.groupKey,
    state.activeDataPoint.key,
    state.activeDataPoint.index,
  ]);
